<template>
  <div class="flip-container" :class="{'flip': shouldFlip}">
    <div class="flipper">
      <div class="game-square front">&nbsp;</div>
      <div class="game-square back" :class="{ 'winner': isWinner }">
        {{ mark }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GameSquare',
  props: {
    mark: { type: String, default: null },
    isWinner: { type: Boolean, default: false }
  },
  computed: {
    shouldFlip () {
      return !!this.mark
    }
  }
}
</script>

<style lang="scss">
.game-square {
  border: none;
  background-color: hsl(212, 20%, 97%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  height: 100%;
  width: 100%;
  transition: background-color 0.6s;
  &:hover {
    background-color: hsl(212, 20%, 92%);
  }
  &.winner {
    background-color: hsl(153, 48%, 88%);
  }
}

/* Flipper classes */
/* entire container, keeps perspective */
.game-square {
  perspective: 1000px;
  perspective-origin: center;
}
/* flip the pane when clicked */
.flip-container.flip .flipper {
  transform: rotateY(180deg);
}

.flip-container,
.flipper,
.front,
.back {
  width: 100%;
  height: 100%;
}

/* flip speed goes here */
.flipper {
  transition: 0.6s;
  transform-style: preserve-3d;

  position: relative;
}

/* hide back of pane during swap */
.front,
.back {
  backface-visibility: hidden;

  position: absolute;
  top: 0;
  left: 0;
}

/* front pane, placed above back */
.front {
  z-index: 2;
  /* for firefox 31 */
  transform: rotateY(0deg);
}

/* back, initially hidden pane */
.back {
  transform: rotateY(180deg);
}
</style>



