<template>
  <section class="game-board">
    <game-square
      v-for="(mark, index) in marks"
      :key="`gbs-${index}`"
      :mark="mark"
      :isWinner="winningPattern.includes(index)"
      @click.native="$emit('setMark', index)" />
    <transition name="fade">
      <div class="game-winner" v-if="isDraw">It's a draw.</div>
      <div class="game-winner" v-else-if="winner">{{ winner }} is the winner!</div>
    </transition>
  </section>
</template>

<script>
import GameSquare from '@/components/GameSquare'

export default {
  name: 'GameBoard',
  props: {
    marks: { type: Array, required: true },
    winner: { type: String, default: null },
    winningPattern: { type: Array, default: () => [] }
  },
  components: { GameSquare },
  computed: {
    isDraw () {
      return this.winner && !this.winningPattern.length
    }
  }
}
</script>

<style lang="scss">
.game-board {
  width: 90vmin;
  height: 90vmin;
  max-width: calc(90vh - 234px);
  max-height: calc(90vh - 234px);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 0.25em;
  justify-items: stretch;
  align-items: stretch;
  border: none;
  border-radius: 0.25em;
  box-shadow: 1px 1px 5px 0 hsl(0, 0%, 77%);
  background-color: hsl(212, 27%, 29%);
  overflow: hidden;
}

.game-winner {
  background-color: hsl(153, 48%, 49%);
  color: hsl(0, 0%, 100%);
  font-size: 1.4rem;
  font-weight: 500;
  grid-column: 1 / span 3;
  height: 2.5rem;
  padding: 0.25em;
  text-align: center;
}
// transitions
.fade-enter-active {
  transition: opacity 0.6s;
}
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



