<template>
  <div id="app">
    <main class="game">
      <header>
        <img src="./assets/logo.png" class="logo" alt="Vue.js logo">
        <h1 class="app-title">Tic-Tac-Toe</h1>
      </header>
      <section class="score">
        <span>{{ players.x.name }}: {{ players.x.wins }} </span>
        <span>Total Games: {{ gameCount }} </span>
        <span>{{ players.o.name }}: {{ players.o.wins }} </span>
      </section>
      <GameBoard
        :marks="marks"
        :winner="winner ? winner.name : null"
        :winningPattern="winningPattern"
        @setMark="setMark"
        style="margin: 0 auto;"
      />
    </main>
    <section class="toolbar">
      <button class="button isQuiet" @click="resetScore">Reset Score</button>
      <button class="button isOutline" @click="resetMarks">New Game</button>
    </section>
  </div>
</template>

<script>
import GameBoard from '@/components/GameBoard'

export default {
  name: 'App',
  components: {
    GameBoard
  },
  data () {
    return {
      marks: [null, null, null, null, null, null, null, null, null],
      nextMark: null,
      players: {
        x: { name: 'X', mark: 'X', wins: 0 },
        o: { name: 'O', mark: 'O', wins: 0 },
        d: { name: 'Draw', mark: 'D', wins: 0 }
      }
    }
  },
  created () {
    this.setNextMark()
  },
  methods: {
    resetMarks () {
      this.marks = [null, null, null, null, null, null, null, null, null]
      this.setNextMark()
    },
    resetScore () {
      for (let key in this.players) {
        this.players[key].wins = 0
      }
      this.resetMarks()
    },
    setMark (square) {
      if (this.winner) return
      if (this.marks[square] !== null) return

      this.marks.splice(square, 1, this.nextMark)
      this.setNextMark()
    },
    setNextMark () {
      if (this.isFreshGame) {
        // alternate the starting player for each game
        this.nextMark =
          this.gameCount % 2 ? this.players.o.mark : this.players.x.mark
      } else {
        this.nextMark =
          this.nextMark === this.players.x.mark
            ? this.players.o.mark
            : this.players.x.mark
      }
    }
  },
  computed: {
    isFreshGame () {
      return !this.marks.find(mark => mark !== null)
    },
    gameCount () {
      return Object.values(this.players).reduce(
        (total, player) => total + player.wins,
        0 // starting value for total
      )
    },
    winningPattern () {
      const patterns = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6]
      ]

      for (let [a, b, c] of patterns) {
        if (
          this.marks[a] === this.marks[b] &&
          this.marks[a] === this.marks[c] &&
          this.marks[a] !== null
        ) {
          return [a, b, c] // the matching pattern
        }
      }
      return []
    },
    winner () {
      // find the winning player
      if (this.winningPattern.length) {
        return Object.values(this.players).find(
          player => player.mark === this.marks[this.winningPattern[0]]
        )
      }
      // check for a draw
      if (!this.marks.includes(null)) return this.players.d
      // game is not over yet
      return null
    }
  },
  watch: {
    winner (player, lastPlayer) {
      if (player) player.wins++
    }
  }
}
</script>

<style lang="scss">
$base-colour: hsl(212, 27%, 29%);
$secondary-colour: hsl(153, 48%, 49%);
$highlight-colour: adjust-hue($base-colour, -20%);
$inverse-colour: lighten(desaturate($base-colour, 77%), 60%);

html,
body {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
  outline: none;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background: lighten(desaturate($secondary-colour, 40%), 44%);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto;
  justify-items: center;
  align-items: center;
  height: 100%;

  .logo {
    width: 90px;
    height: auto;
  }
}

.app-title {
  margin-top: 0;
}

.button {
  $grey: hsl(212, 20%, 77%);
  background: hsl(0, 0%, 98%);
  border: 1px solid $grey;
  border-radius: 0.25em;
  color: $base-colour;
  font-size: 1.1em;
  margin: 1em;
  outline: none;
  padding: 0.5em 1em;

  &.isPrimary {
    background: linear-gradient(to right, $base-colour, $highlight-colour);
    border-color: $base-colour;
    box-shadow: 0 1px 5px 1px $grey;
    color: $inverse-colour;
  }

  &.isOutline {
    background: transparent;
  }

  &.isQuiet {
    background: transparent;
    border-color: transparent;
  }

  &:hover {
    background: linear-gradient(
      to right,
      lighten($base-colour, 5%),
      lighten($highlight-colour, 5%)
    );
    border-color: lighten($base-colour, 5%);
    box-shadow: 0 1px 5px 2px hsl(0, 0%, 67%);
    color: $inverse-colour;
  }
  &:active {
    background: linear-gradient(
      to right,
      darken($base-colour, 5%),
      darken($highlight-colour, 5%)
    );
    border-color: darken($base-colour, 5%);
    box-shadow: none;
  }
}
.score {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 1.15em;
  font-weight: 400;
  // color: darken($secondary-colour, 18%);
  margin-bottom: 1em;
}
</style>



